.bought-courses {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: calc(var(--index)*2);
}

.course__info {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    border-radius: calc(var(--index)*0.5);
    position: relative;
    background-color: var(--other-bg-color);

    &::after {
        content: "";
        position: absolute;
        width: calc(var(--index)*0.15);
        height: 85%;
        background-color: var(--main-color);
        left: 50%;
        border-radius: calc(var(--index)*6);
    }
}

.course__info-img {
    width: 47%;
    height: calc(var(--index)*18);
    overflow: hidden;
    cursor: pointer;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: 0.25s ease;

        &:hover {
            transform: scale(1.1);
        }
    }
}

.course__info-info {
    width: 47%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: calc(var(--index)*1.1);
    padding: calc(var(--index)*2);
    padding-left: 0;

    .spec {
        width: 100%;
        display: grid;
        grid-template-columns: auto 1fr auto;
        gap: calc(var(--index)*0.2);

        .dots {
            width: 100%;
            border-bottom: calc(var(--index)*0.16) dotted rgba(0, 0, 0, 0.425);
        }

        p {
            font-size: calc(var(--index)*1);
        }
    }

    .progress__info {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: calc(var(--index)*0.5);
        padding: calc(var(--index)*0.5) 0;
        padding-bottom: 0;

        p {
            font-size: calc(var(--index)*0.8);
        }

        .progress {
            width: 100%;
            height: calc(var(--index)*0.5);
            border-radius: calc(var(--index)*6);
            overflow: hidden;
            background-color: var(--border-color);

            .progress__progress {
                width: 50%;
                height: 100%;
                border-radius: calc(var(--index)*6);
                background-color: var(--main-color);
            }
        }
    }
}

.bought__courses {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: calc(var(--index)*1.3);

    h1 {
        font-size: calc(var(--index)*2);
    }
}

.bought__courses-courses {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--index)*1.5);
    background-color: var(--other-bg-color);
    border-radius: calc(var(--index)*0.5);
    padding: calc(var(--index)*1.3);

    .bought__courses-div {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        // gap: calc(var(--index)*1);
        overflow: hidden;
        border-radius: calc(var(--index)*0.5);
        background-color: var(--bg-color);

        .bought__courses-img {
            width: 100%;
            height: calc(var(--index)*10);
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                transition: 0.25s ease;
                object-fit: cover;

                &:hover {
                    transform: scale(1.1);
                }
            }
        }

        .bought__courses__container {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: calc(var(--index)*1);
            padding: calc(var(--index)*1);

            h3 {
                font-size: calc(var(--index)*1.3);
            }

            .bought__courses-info {
                width: 100%;
                height: auto;
                padding: calc(var(--index)*0.8);
                display: flex;
                justify-content: space-between;
                background: var(--other-bg-color);
                border-radius: calc(var(--index)*0.25);

                .bought__due-date,
                .bought__courses-videos {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: calc(var(--index)*0.8);

                    p {
                        font-size: calc(var(--index)*0.9);
                        font-weight: 500;
                    }

                    p:nth-child(1) {
                        color: var(--main-color);
                    }
                }

                .bought__courses-videos {
                    align-items: flex-end;
                    
                    p{
                        text-align: end;
                    }
                }
            }

            .progress__info {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: calc(var(--index)*0.5);

                .progress {
                    width: 100%;
                    height: calc(var(--index)*0.5);
                    border-radius: calc(var(--index)*6);
                    overflow: hidden;
                    background-color: var(--border-color);

                    .progress__progress {
                        width: 50%;
                        height: 100%;
                        border-radius: calc(var(--index)*6);
                        background-color: var(--main-color);
                    }
                }

                p {
                    font-size: calc(var(--index)*0.8);
                }
            }
        }
    }
}

@media (max-width: 700px) {
    .bought__courses-courses {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 560px) {
    .course__info {
        width: 80%;
        flex-direction: column;
        gap: calc(var(--index)*2);

        &::after {
            display: none;
        }
    }

    .course__info-img {
        width: 100%;
    }

    .course__info-info {
        width: 85%;
        padding: 0;
        padding-bottom: calc(var(--index)*1.3);
        gap: calc(var(--index)*2);

        .spec {
            .dots {
                border-bottom: calc(var(--index)*0.19) dotted rgba(0, 0, 0, 0.425);
            }

            p {
                font-size: calc(var(--index)*1.2);
            }
        }

        .progress__info {
            gap: calc(var(--index)*1.3);

            p {
                font-size: calc(var(--index)*1.3);
            }

            .progress {
                height: calc(var(--index)*0.8);
            }
        }
    }
}

@media (max-width: 380px) {
    .course__info {
        width: 100%;
    }

    .bought__courses-courses {
        grid-template-columns: repeat(1, 1fr);

        .bought__courses-div {
            .bought__courses__container {
                gap: calc(var(--index)*2.3);

                .bought__courses-info {

                    .bought__due-date,
                    .bought__courses-videos {
                        gap: calc(var(--index)*2);

                        p {
                            font-size: calc(var(--index)*1.3);
                        }
                    }
                }

                .progress__info {
                    .progress {
                        height: calc(var(--index)*0.8);
                    }

                    p {
                        font-size: calc(var(--index)*1.3);
                    }
                }

                h3 {
                    font-size: calc(var(--index)*2);
                }
            }
        }
    }
}